  .dropzone {
    text-align: center;
    padding: 20px;
    border: 3px dashed #0d0b0b91;
    background-color: #33323271;
    color: #bdbdbd;
    width: 70%;
    height:25vh;
    margin-left: 15%;
    margin-bottom: 20px;
  }
  /* photos.css */
.thumbnails-container {
  display: flex;
  flex-wrap: wrap;
}

.thumbnail {
  display: inline-block;
  margin: 10px;
  text-align: center;
}

.thumbnail-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
}

  